/**
 * Main.less
 *
 * Add all your custom CSS here
 */
.auto {
  margin: 0 auto;
  padding: 0 auto;
}
.vertical-align {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}
/**
 * Define default globals
 */
html,
body {
  text-rendering: geometricPrecision;
  font-smoothing: antialiased;
  font-size: 12.5px;
  position: relative;
  background: #efefef;
  height: auto;
}
html,
body,
p,
label,
input,
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Source Sans Pro', sans-serif;
}
body {
